import React from 'react'
import Categories from "../components/Categories";
import PizzaBlock from '../components/PizzaBlock';
import SortPopup from "../components/SortPopup";

import { usePizzas } from "../hooks/usePizzas";
import { useDispatch, useSelector } from "react-redux";
import { setSortBy, setCategory } from "../redux/slices/filtersSlice";
import {addToCart} from '../redux/slices/cartSlice'
import LoadingBlock from '../components/LoadingBlock';

const categoryNames = [
    "–ú—è—Å–Ω—ã–µ",
    "–í–µ–≥–µ—Ç–∞—Ä–∏–∞–Ω—Å–∫–∞—è",
    "–ì—Ä–∏–ª—å",
    "–û—Å—Ç—Ä—ã–µ",
    "–ó–∞–∫—Ä—ã—Ç—ã–µ",
];
const sortIems = [
    { name: '–ø–æ–ø—É–ª—è—Ä–Ω–æ—Å—Ç–∏', type: 'rating', order: 'desc' },
    { name: '—Ü–µ–Ω–µ', type: 'price', order: 'desc' },
    { name: '–Ω–∞–∑–≤–∞–Ω–∏—é', type: 'name', order: 'asc' },
];


const Home = () => {
    const dispatch = useDispatch();
    const { pizzas, status, error } = useSelector(state => state.pizzas);
    const { category, sortBy } = useSelector(state => state.filters);
    const { items } = useSelector(state => state.cart);
    const sortedAndCategoryPizzas = usePizzas(pizzas, category, sortBy);

    const onSelectCategory = (index) => {
        dispatch(setCategory(index));
    }

    const onSelectSortType = (type) => {
        dispatch(setSortBy(type));
    }

    const handleAddPizzaToCart = (obj) => {
        dispatch(addToCart(obj));
      };

    return (
        <div className="container">
            <div className="content__top">
                <Categories
                    activeCategory={category}
                    onClickItem={onSelectCategory}
                    items={categoryNames}
                />
                <SortPopup
                    activeSortType={sortBy}
                    items={sortIems}
                    onClickSortType={onSelectSortType}
                />
            </div>
            <h2 className="content__title">–í—Å–µ –ø–∏—Ü—Ü—ã</h2>
            <div className="content__items">
                {error && <h2>An error occured: {error}</h2>}
                {status !== 'loading'
                    ? sortedAndCategoryPizzas && sortedAndCategoryPizzas.map((obj) => (
                        <PizzaBlock
                            key={obj.id}
                            addedCount={items[obj.id] && items[obj.id].items.length} {...obj}
                            onClickAddPizza={handleAddPizzaToCart}
                        />))
                    : Array(12)
                        .fill(0)
                        .map((_, index) => <LoadingBlock key={index} />)
                }
            </div>
        </div>
    )
}

export default Home;
